//WordPress dependencies
import { RichText, useBlockProps } from '@wordpress/block-editor';
import { __ } from '@wordpress/i18n';
//external dependencies
import classnames from 'classnames';
import { applyFilters } from '@wordpress/hooks';
//internal dependencies
import Inspector from './inspector';
import './style.scss';

const { DisplayZoloIcon, classArrayToStr, SidebarOpener } = window.zoloModule;

// import style
import Style from './style';

const Edit = (props) => {
    const { attributes, setAttributes, isSelected, clientId } = props;
    const {
        preset,
        preview,
        uniqueId,
        parentClasses,
        //layout
        styles,
        //header
        titleText,
        titleTagName,
        showDesc,
        descText,
        //price
        orginalPrice,
        pricePrefix,
        priceSuffix,
        price,
        sale,
        period,
        //features
        showFeatures,
        hideFeatureIcon,
        showFeatureHeading,
        showFeatureDesc,
        featureTitle,
        featureDesc,
        features,

        //buttons
        showBtn,
        showChatBtn,
        buttonText,
        chatBtnText,

        //ribbon
        showRibbon,
        ribbonTitle,
        ribbonPosition,

        // extra
        btnsPosition,
        btnsDirection,
    } = attributes;

    // this useEffect is for creating a unique id for each block's unique className by a random unique number

    //block wrapper class
    const blockProps = useBlockProps({
        className: classnames(uniqueId, classArrayToStr(parentClasses), preset !== '' && preset),
    });

    // filter hooks for render
    const renderHookBefore = applyFilters('zolo.blocks.render.hook.before', [], props);
    const renderHookAfter = applyFilters('zolo.blocks.render.hook.after', [], props);

    const pricingPeriod = period.length !== 0 && period.split(',');

    // preview image
    if (preview) {
        return <img src={zoloParams.blocksPreview.pricingTable} alt={__('Pricing Table Preview', 'zoloblocks')} />;
    }

    return (
        <>
            {isSelected && <Inspector attributes={attributes} setAttributes={setAttributes} />}

            <Style props={props} />

            <div {...blockProps}>
                {renderHookBefore && renderHookBefore}
                <SidebarOpener clientId={clientId} />
                <div className="zolo-item">
                    <div className="zolo-head-content">
                        <RichText
                            tagName={titleTagName}
                            className="zolo-package-title"
                            value={titleText}
                            onChange={(titleText) => setAttributes({ titleText })}
                            placeholder={__('Add service name', 'zoloblocks')}
                            allowedFormats={['bold', 'italic', 'strikethrough']}
                        />

                        {showRibbon && ribbonTitle && (
                            <div className={`zolo-ribbon-btn ${ribbonPosition}`}>
                                <RichText tagName="span" value={ribbonTitle} onChange={(v) => setAttributes({ ribbonTitle: v })} />
                            </div>
                        )}

                        <div className="zolo-price-info">
                            {orginalPrice && sale && (
                                <span className="zolo-orginal-price">
                                    {pricePrefix && <span className="currency">{pricePrefix}</span>}
                                    <span className="price">{orginalPrice}</span>
                                </span>
                            )}

                            {price && (
                                <span className="zolo-price">
                                    {pricePrefix && <span className="currency">{pricePrefix}</span>}
                                    <span className="price">{price}</span>
                                    {priceSuffix && <span className="fractional">{priceSuffix}</span>}
                                </span>
                            )}

                            {pricingPeriod && pricingPeriod.length > 0 && (
                                <div className="zolo-user-month">
                                    {pricingPeriod.map((name, index) => (
                                        <span className={`zolo-period text-${index}`} key={index}>
                                            {name}
                                        </span>
                                    ))}
                                </div>
                            )}
                        </div>

                        {showDesc && (
                            <RichText
                                tagName="div"
                                className="zolo-package-desc"
                                value={descText}
                                onChange={(descText) => setAttributes({ descText })}
                                placeholder={__('Add description', 'zoloblocks')}
                                allowedFormats={['bold', 'italic', 'strikethrough']}
                            />
                        )}
                        {btnsPosition === 'middle' && (
                            <>
                                {(showBtn || showChatBtn) && (
                                    <div className={`zolo-link-btn ${btnsDirection}`}>
                                        {showBtn && (
                                            <RichText
                                                tagName="a"
                                                className="zolo-buy-btn"
                                                value={buttonText}
                                                onChange={(text) => setAttributes({ buttonText: text })}
                                                placeholder={__('Button Text', 'zoloblocks')}
                                                allowedFormats={[]}
                                            />
                                        )}
                                        {showChatBtn && (
                                            <RichText
                                                tagName="a"
                                                className="zolo-chat-btn"
                                                value={chatBtnText}
                                                onChange={(text) => setAttributes({ chatBtnText: text })}
                                                placeholder={__('Button Text', 'zoloblocks')}
                                                allowedFormats={[]}
                                            />
                                        )}
                                    </div>
                                )}
                            </>
                        )}
                    </div>

                    <div className="zolo-features-info">
                        {showFeatureHeading && (
                            <RichText
                                tagName="div"
                                className="zolo-features-title"
                                value={featureTitle}
                                onChange={(featureTitle) => setAttributes({ featureTitle })}
                                placeholder={__('Add feature title', 'zoloblocks')}
                                allowedFormats={['bold', 'italic', 'strikethrough']}
                            />
                        )}
                        {showFeatureDesc && (
                            <RichText
                                tagName="div"
                                className="zolo-features-desc"
                                value={featureDesc}
                                onChange={(featureDesc) => setAttributes({ featureDesc })}
                                placeholder={__('Add feature description', 'zoloblocks')}
                                allowedFormats={['bold', 'italic', 'strikethrough']}
                            />
                        )}

                        {features.length !== 0 && showFeatures && (
                            <ul className="features">
                                {features.map((item, index) => (
                                    <li key={index}>
                                        {item.icon && hideFeatureIcon !== true && (
                                            <span className="zolo-check-icon">
                                                <DisplayZoloIcon icon={item.icon} />
                                            </span>
                                        )}
                                        <span className="zolo-list-text">{item.text}</span>
                                    </li>
                                ))}
                            </ul>
                        )}

                        {btnsPosition === 'bottom' && (
                            <>
                                {(showBtn || showChatBtn) && (
                                    <div className={`zolo-link-btn ${btnsDirection}`}>
                                        {showBtn && (
                                            <RichText
                                                tagName="a"
                                                className="zolo-buy-btn"
                                                value={buttonText}
                                                onChange={(text) => setAttributes({ buttonText: text })}
                                                placeholder={__('Button Text', 'zoloblocks')}
                                                allowedFormats={[]}
                                                title={buttonText}
                                            />
                                        )}
                                        {showChatBtn && (
                                            <RichText
                                                tagName="a"
                                                className="zolo-chat-btn"
                                                value={chatBtnText}
                                                onChange={(text) => setAttributes({ chatBtnText: text })}
                                                placeholder={__('Button Text', 'zoloblocks')}
                                                allowedFormats={[]}
                                                title={chatBtnText}
                                            />
                                        )}
                                    </div>
                                )}
                            </>
                        )}
                    </div>
                </div>
                {renderHookAfter && renderHookAfter}
            </div>
        </>
    );
};

export default Edit;
